// Action types
import { DISPLAY, EVALUATE, HISTORY } from '../actions/actions';

// Action creator for displaying expression
export const getDisplayAction = (input) => {
    return {
        type: DISPLAY,
        text: input
    }
};

// Action creator for evaluating the expression
export const getEvaluateAction = (input) => {
    return {
        type: EVALUATE,
        text: input
    }
}
//Action creator for History
export const getHistoryAction = (input) => {
    return {
        type: HISTORY,
        text: input
    }
}